.contacts--catalog {
  background-color: $basic_grey;
}

.contacts__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 25px 0;
}

.contacts__slogan {
  font-family: $font_oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
  color: $special_black;
  text-align: start;
  text-transform: uppercase;
  margin: 0;
  width: 50%;
}

.contacts__address {
  font-size: 14px;
  line-height: 1.43;
  margin: 0;
  text-align: start;
  width: 50%;
}

.contacts__address br {
  display: none;
}

.contacts--form {
  background-color: $basic_grey;
}

.contacts iframe {
  border: none;
  display: block;
  width: 100%;
}

@media (min-width: $tablet-width) {
  .contacts iframe {
    height: 400px;
  }

  .contacts__wrapper {
    padding: 56px 40px 56px 40px;
    box-sizing: border-box;
  }

  .contacts__slogan {
    width: 170px;
    font-size: 20px;
  }

  .contacts__address {
    width: 160px;
    text-align: right;
    font-size: 16px;
    line-height: 1.25;
  }

  .contacts__address br {
    display: unset;
  }
}

@media (min-width: $desktop-width) {
  .contacts {
    position: relative;
  }

  .contacts__wrapper {
    position: absolute;
    background-color: $white;
    width: 565px;
    padding: 55px 80px;
    box-sizing: border-box;
    top: 96px;
    left: 110px;
  }

  .contacts__address {
    text-align: left;
  }
}
